.white  {
  color: white !important;
}
body {
  /* background-color: #121212; */
  background-color: #0c1318 /*this looks nice*/
}

.player-fluid {
  background-color: black;
  position: sticky;
  bottom: 0;
  height: 70px;
  padding: 10px;
  color: white
}
.player {
  display: inline-flex;
  background-color: black;
  position: sticky;
  bottom: 0;
  height: 70px;
  padding: 10px;
  color: white;
  width: 100%;
}
svg {
  fill: white;
}
.card {
  /* box-shadow: 0 4px 8px 0 white, 0 6px 20px 0 white; */
}
.empty-card{
  /* background-color : #121212; */
  background-color: #0c1318
}

.ball {
  width: 15px;
  height: 15px;
  border-radius: 10px;
  top: -6px;
  position: absolute;
  background-color: #007bff;
}
.done , .left {
  background-color: #007bff;
  height: 2px;
  width: 300px;
  position: absolute;
  top: 60%;
}
.time {
  color: #007bff;
  top: 30%;
  right: 450px;
  position: absolute;
}
/* https://www.iconsdb.com/white-icons/fast-forward-icon.html */
.play, .next, .prev, .pause {
  width: 30px;
  height: 30px; 
}

.gutter {
  top: 30px;
  position: absolute;
}

